<!-- 上网软件记录-->
<template>
  <div class="check_tab">
    <el-tabs class="tab_class" type="border-card">
      <el-tab-pane label="上网软件记录">
        <el-table
          :data="tableData"
          ref="multipleTable"
          tooltip-effect="dark"
          style="width: 100%"
          border
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column label="序号" width="50" type="index" sortable></el-table-column>
          <el-table-column prop="fileName" label="上网情况" sortable min-width="110px"></el-table-column>
          <el-table-column prop="keyWords" label="安装路径" sortable min-width="110px"></el-table-column>
          <el-table-column prop="hitRate" label="上网时间" sortable min-width="110px"></el-table-column>
          <el-table-column prop="classified" label="软件名称" sortable min-width="110px"></el-table-column>
          <el-table-column prop="classified" label="软件版本" sortable min-width="110px"></el-table-column>
          <el-table-column prop="classified" label="软件类型" sortable min-width="110px"></el-table-column>
          <el-table-column prop="classified" label="访问账户" sortable min-width="110px"></el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="上网统计">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
          border
        >
          <el-table-column label="序号" width="50" type="index" sortable></el-table-column>
          <el-table-column prop="fileName" label="软件名称" sortable width="150"></el-table-column>
          <el-table-column prop="keyWords" label="上网数量" sortable></el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <div class="check_dispose">
      <el-button @click="showDialog = true">设置检查时间</el-button>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      tableData: [
        // {
        //   fileName: "", //文件名称
        //   keyWords: "", //命中关键词
        //   hitRate: "", //关键词命中率
        //   classified: "", //涉密情况
        // }
      ],
      showDialog: false,
      options: [
        {
          value: "选项1",
          label: "下载可信网址列表模板"
        }
      ],
      value1: "",
      options2: [
        {
          value: "选项1",
          label: "添加可信网址"
        },
        {
          value: "选项2",
          label: "修改可信网址"
        }
      ],
      value2: "",
      options3: [
        {
          value: "选项1",
          label: "导入可信网址列表"
        }
      ],
      value3: ""
    };
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
};
</script>

